<template>
  <v-container
    id="dashboard"
    fluid
    tag="section"
  >
    <show-dialog-slider-empresas
      v-bind="{ value }"
      @procesar="procesar($event)"
      @cerrar="cerrar()"
    />
    <v-row>
      <v-col cols="12">
        <base-material-card
          color="success"
          icon="mdi-view-carousel"
          inline
          title="Slider Empresas"
          class="px-5 py-3 mb-5"
        >
          <v-row>
            <v-col class="pa-0 ma-0 text-right">
              <v-btn
                color="success"
                class="ma-0"
                @click="abrirCrear()"
              >
                Nuevo Slider
              </v-btn>
            </v-col>
          </v-row>
          <v-simple-table>
            <thead>
              <tr>
                <th>Imagen</th>
                <th>Imagen Mobile</th>
                <th class="text-right">
                  Fecha Cre.
                </th>
                <th class="text-right">
                  Fecha Mod.
                </th>
                <th class="text-right">
                  Orden
                </th>
                <th>Acciones</th>
              </tr>
            </thead>

            <tbody>
              <tr
                v-for="(slider, index) in sliders"
                :key="index"
              >
                <td>{{ slider.Imagen }}</td>
                <td>{{ slider.ImagenMobile }}</td>
                <td class="text-right">
                  {{ getDateByDateTime(slider.FechaHoraCreacion) }}
                </td>
                <td class="text-right">
                  {{ getDateByDateTime(slider.FechaHoraModificacion) }}
                </td>
                <td>{{ slider.Orden }}</td>
                <td class="text-right">
                  <v-btn
                    class="px-2 ml-1"
                    color="warning"
                    min-width="0"
                    small
                    @click="abrirEditar(slider.Id)"
                  >
                    <v-icon
                      small
                      v-text="'mdi-pencil'"
                    />
                  </v-btn>
                  <v-btn
                    class="px-2 ml-1"
                    color="error"
                    min-width="0"
                    small
                    @click="eliminar(slider.Id)"
                  >
                    <v-icon
                      small
                      v-text="'mdi-trash-can'"
                    />
                  </v-btn>
                </td>
              </tr>
            </tbody>
          </v-simple-table>
        </base-material-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import Vue from 'vue'
  import { mapState } from 'vuex'
  import mixins from '@/mixins'
  export default {
    name: 'SlidersEmpresas',
    components: {
      ShowDialogSliderEmpresas: () => import('@/components/base/ShowDialogSliderEmpresas'),
    },
    mixins: [mixins],
    data () {
      return {
        sliders: '',
        value: {
          slider: {
            Id: false,
            Imagen: '',
            ImagenMobile: '',
            HrefLink: '',
            ToLink: '',
            Orden: '',
          },
          imagen: null,
          imagenMobile: null,
          nombreImagen: '',
          nombreImagenMobile: '',
          chkImagen: false,
          chkImagenMobile: false,
          loader: false,
          active: false,
        },
      }
    },
    computed: {
      ...mapState(['token']),
    },
    created () {
      this.getSlidersEmpresas()
    },
    methods: {
      getSlidersEmpresas () {
        Vue.axios
          .get('/api/sliders-empresas')
          .then(res => {
            this.sliders = res.data.result
          })
          .catch(err => {
            console.log(err)
          })
      },
      getSliderEmpresas (id) {
        Vue.axios
          .get(`/api/sliders-empresas/${id}`)
          .then(res => {
            this.value.slider = res.data.result[0]
          })
          .catch(err => {
            console.log(err)
          })
      },
      abrirCrear () {
        this.value.active = true
        this.value.chkImagen = true
        this.value.chkImagenMobile = true
      },
      abrirEditar (id) {
        this.getSliderEmpresas(id)
        this.value.active = true
        this.value.chkImagen = false
        this.value.chkImagenMobile = false
      },
      cerrar () {
        this.value = {
          slider: {
            Id: false,
            Imagen: '',
            ImagenMobile: '',
            HrefLink: '',
            ToLink: '',
            Orden: '',
            VersionImagen: '',
          },
          imagen: null,
          imagenMobile: null,
          nombreImagen: '',
          nombreImagenMobile: '',
          chkImagen: false,
          chkImagenMobile: false,
          loader: false,
          active: false,
        }
      },
      procesar (val) {
        this.value.loader = true
        const headers = {
          'x-token': this.token,
        }
        console.log(val.slider)
        const body = new FormData()
        body.append('id', val.slider.Id)
        body.append('hrefLink', val.slider.HrefLink)
        body.append('toLink', val.slider.ToLink)
        body.append('orden', val.slider.Orden)
        body.append('imagen', val.imagen)
        body.append('imagenMobile', val.imagenMobile)
        body.append('nombreImagen', val.nombreImagen)
        body.append('nombreImagenMobile', val.nombreImagenMobile)

        if (val.slider.Id) {
          this.update(body, headers)
        } else {
          this.create(body, headers)
        }
      },
      create (body, headers) {
        Vue.axios
          .post('/api/sliders-empresas', body, { headers })
          .then(res => {
            this.sliders.unshift(res.data.Slider[0])
            this.cerrar()
            Vue.swal('Slider creado', '', 'success')
          })
          .catch(err => {
            this.swalAlertError(err)
            this.cerrar()
          })
      },
      update (body, headers) {
        Vue.axios
          .put('/api/sliders-empresas', body, { headers })
          .then(res => {
            const index = this.sliders.findIndex(s => s.Id === Number(res.data.Slider[0].Id))
            this.sliders[index] = res.data.Slider[0]
            this.cerrar()
            Vue.swal('Slider actualizado', '', 'success')
          })
          .catch(err => {
            this.swalAlertError(err)
            this.cerrar()
          })
      },
      eliminar (id) {
        Vue.swal({
          title: 'Estás seguro(a) que deseas borrar este slider?',
          showConfirmButton: true,
          showCancelButton: true,
          confirmButtonText: 'Eliminar',
          cancelButtonText: 'Cancelar',
          confirmButtonColor: '#ff5252',
          cancelButtonColor: '#a1a1a1',
        }).then(result => {
          if (result.isConfirmed) {
            Vue.axios
              .delete(`/api/sliders-empresas/${id}`, {
                headers: { 'x-token': this.token },
              })
              .then(res => {
                const index = this.sliders.findIndex(s => s.Id === Number(res.data.id))
                this.sliders.splice(index, 1)
                Vue.swal('Slider Eliminado', '', 'success')
              })
              .catch(err => {
                this.swalAlertError(err)
                this.cerrar()
              })
          }
        })
      },
    },
  }
</script>
